export default {
	namespaced: true,
	state: {
		id: 0, //ID
		isLogin: false, // 
		username: '', // 
		nickname: '', // 
		mobile: '', // 
		avatar: '', // 
		level: 0, // 
		gender: 0, // 
		birthday: '', // 
		bio: '', // 
		money: '0.00', // 
		score: 0, // 
		successions: '', // 
		maxsuccessions: '', // 
		prevtime: '', // 
		logintime: '', // 
		loginip: '', // IP
		jointim: '', // 
		token: '', // 
		pushs: true, // 
		shock: true, // 
		voice: true // 
	},
	mutations: {
		setUserInfo(state, payload) {
			for (let i in payload) {
				for (let j in state) {
					if (i === j) {
						state[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:user", state);
		}
	},
	actions: {
		async login({state, commit, dispatch, rootState}, data) {
			commit('setUserInfo', data.userinfo);
			state.isLogin = true; //  
			// notice.vue fastadmin
			state.pushs = true; // 
			state.voice = true; // 
			state.shock = true; // 
			uni.setStorageSync("wanlshop:user", state);
			// 
			dispatch('statistics', data.statistics)
		},
		async logout({state, rootState}) {
			for (let j in state) {
				state[j] = ''
			}
			state.isLogin = false;
			let statistics = rootState.statistics;
			for (let j in statistics) {
				for (let i in statistics[j]) {
					statistics[j][i] = 0
				}
			}
			// key
			uni.removeStorageSync('wanlshop:user');
			uni.removeStorageSync('wanlshop:statis');
			// 
			uni.closeSocket();
		},
		async statistics({state, dispatch, rootState}, payload){
			let states = rootState.statistics;
			for (let i in payload) {
				for (let j in states) {
					if (i === j) {
						states[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:statis", states);
		}
		
	}
};