export default {
	namespaced: true,
	// 
	state: {
		// 
		order: {
			pay: 0,
			delive: 0,
			receiving: 0,
			evaluate: 0,
			customer: 0 //
		},
		// 
		dynamic: {
			collection: 0,
			concern: 0,
			footprint: 0,
			coupon: 0, // 
			accountbank: 0 // 
		},
		// 
		notice: {
			order: 0,
			notice: 0,
			chat: 0
		},
		// 
		logistics: []
	},
	// 
	mutations: {
		// 
		edit(state, payload){
			for (let i in payload) {
				for (let j in state) {
					if (i === j) {
						state[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:statis", state);
		},
		//  - 
		order(state, payload) {
			for (let i in payload) {
				for (let j in state.order) {
					if (i === j) {
						state.order[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:statis", state);
		},
		//  - 
		dynamic(state, payload) {
			for (let i in payload) {
				for (let j in state.dynamic) {
					if (i === j) {
						state.dynamic[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:statis", state);
		},
		//  - 
		noticec(state, payload) {
			for (let i in payload) {
				for (let j in state.notice) {
					if (i === j) {
						state.notice[j] = payload[i];
					}
				}
			}
			uni.setStorageSync("wanlshop:statis", state);
		}
	},
	actions: {
		async get({commit, rootState}) {
			commit('edit', uni.getStorageSync('wanlshop:statis'));
		}
	}
};