class wanlfun {
	
	/**
	 * 
	 * @author  <www.wanlshop.com>
	 */
	setChat(data,send) {
		let uid = send == 'send' ? data.to_id : data.form.id;
		uni.getStorage({
		    key: 'wanlchat:message_' + uid,
		    success: function (res) {
				// 
				// let arr = res.data;
				// , 100,100,
				let arr = res.data.slice(-96);
				arr.push(data);
				uni.setStorageSync('wanlchat:message_' + uid, arr);
		    },
			fail: function (res) {
				uni.setStorageSync('wanlchat:message_' + uid, [data]);
		    }
		});
		// 
		return data;
	}
	
	/**
	 * 
	 * @author  <www.wanlshop.com>
	 * 
	 * @param {Object} number 
	 */
	bcadd(a, b){
	    var c, d, e;
	    try {
	        c = a.toString().split(".")[1].length
	    } catch(f) {
	        c = 0
	    }
	    try {
	        d = b.toString().split(".")[1].length
	    } catch(f) {
	        d = 0
	    }
	    return e = Math.pow(10, Math.max(c, d)),(this.bcmul(a, e) + this.bcmul(b, e)) / e
	}
	
	/**
	 * 
	 * @author  <www.wanlshop.com>
	 * 
	 * @param {Object} number 
	 */
	bcsub(a, b){
	    var c, d, e;
	    try {
	        c = a.toString().split(".")[1].length
	    } catch(f) {
	        c = 0
	    }
	    try {
	        d = b.toString().split(".")[1].length
	    } catch(f) {
	        d = 0
	    }
	    return e = Math.pow(10, Math.max(c, d)),(this.bcmul(a, e) - this.bcmul(b, e)) / e
	}
	
	/**
	 * 
	 * @author  <www.wanlshop.com>
	 * 
	 * @param {Object} number 
	 */
	bcmul(a, b){
	    var c = 0,
	    d = a.toString(),
	    e = b.toString();
	    try {
	        c += d.split(".")[1].length
	    } catch(f) {}
	    try {
	        c += e.split(".")[1].length
	    } catch(f) {}
	    return Number(d.replace(".", "")) * Number(e.replace(".", "")) / Math.pow(10, c)
	}
	
	/**
	 * 
	 * @author  <www.wanlshop.com>
	 * 
	 * @param {Object} number 
	 */
	bcdiv(a, b){
	    var c, d, e = 0,
	    f = 0;
	    try {
	        e = a.toString().split(".")[1].length
	    } catch(g) {}
	    try {
	        f = b.toString().split(".")[1].length
	    } catch(g) {}
	    return c = Number(a.toString().replace(".", "")),d = Number(b.toString().replace(".", "")),this.bcmul(c / d, Math.pow(10, f - e))
	}
}
export default new wanlfun();
